#home {
  position: relative;
  background-image: linear-gradient(100deg, var(--primary-color) 60%, #A7C5E9);

  .app__wrapper {
    padding: 0;

    .copyright {
      display: none;
    }
  }
}

.app__header {
  width: 100%;
  height: 100%;

  flex: 1;
  flex-direction: row;
  padding: 5rem 2rem 0;

  @media screen and (min-width: 2000px) {
    padding-top: 8rem;
  }

  @media screen and (max-width: 1200px) {
    flex-direction: column;
  }

  @media screen and (max-width: 450px) {
    padding: 6rem 1rem 2rem;

  }
}

.app__header-info {
  flex: 0.65;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  height: 100%;

  @media screen and (max-width: 2000px) {
    width: 100%;
    margin-right: 0rem;

  }
}

.app__header-badge {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  flex-direction: column;

  .badge-cmp, .tag-cmp {
    padding: 1rem 2rem;
    border: var(--white-color);
    border-radius: 15px;
    flex-direction: row;
    width: auto;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
  }

  .tag-cmp {
    flex-direction: column;
    margin-top: 1rem;

    .head-text {
      font-size: 2rem;

      @media screen and (max-width: 500px) {
        font-size: 1.5rem;
      }
    }
  }

  @media screen and (max-width: 1200px) {
    justify-content: flex-start;
    align-items: flex-start;
  }
}

.app__header-circles {
  flex: 0.75;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: flex-start;
  height: 100%;
  margin-left: 1rem;

  div {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    background: var(--white-color);
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);

    img {
      width: 60%;
      height: 60%;
    }
  }

  div:nth-child(1) {
    width: 120px;
    height: 120px;
  }

  div:nth-child(2) {
    margin: 0.8rem 1.75rem 0;
    width: 100px;
    height: 100px;
  }

  div:nth-child(3) {
    width: 80px;
    height: 80px;
  }

  div:nth-child(4) {
    margin: 0.3rem 1.75rem 0;
    width: 90px;
    height: 90px;
  }

  div:nth-child(5) {
    width: 70px;
    height: 70px;
  }

  div:nth-child(6) {
    margin: 0.2rem 1.75rem 0;
    width: 70px;
    height: 70px;
  }

  div:nth-child(7) {
    margin: 0.2rem 0.3rem 0;
    width: 70px;
    height: 70px;
  }

  div:nth-child(8) {
    margin: 0.1rem 1rem 0;
    width: 70px;
    height: 70px;
  }

  div:nth-child(9) {
    margin: 0.1rem 1.3rem 0;
    width: 60px;
    height: 60px;
  }

  div:nth-child(10) {
    margin: 0.1rem 0.4rem 0;
    width: 60px;
    height: 60px;
  }

  @media screen and (min-width: 2000px) {
    div:nth-child(1) {
      width: 300px;
      height: 300px;
    }

    div:nth-child(2) {
      width: 150px;
      height: 150px;
    }

    div:nth-child(3) {
      width: 200px;
      height: 200px;
    }

    div:nth-child(4) {
      width: 170px;
      height: 170px;
    }

    div:nth-child(5) {
      width: 140px;
      height: 140px;
    }

    div:nth-child(6) {
      width: 140px;
      height: 140px;
    }
  }

  @media screen and (max-width: 1200px) {
    width: 100%;
    flex-direction: row;
    flex-wrap: wrap;
    margin-left: 0;

    div {
      margin: 1rem;
    }
  }
}

.app__header-img {
  flex: 1;
  height: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  position: relative;

  img {
    width: 100%;
    object-fit: contain;
    z-index: 1;
  }

  .overlay_shape {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 0;
    width: 100%;
    height: 90%;
  }

  @media screen and (max-width: 1200px) {
    margin: 2rem 0;
  }
}

